@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&display=swap);
/* Colors */
html {
  font-size: 62.5%; }

* {
  font-family: "Open Sans";
  font-weight: 400; }

.content-container {
  width: 100%;
  background: #f9f9f9; }

.layout {
  display: flex;
  height: 100vh;
  width: 100%;
  margin: 0 auto; }

.main-title {
  text-transform: uppercase;
  color: #ffffff;
  font-size: 3.6rem;
  font-weight: 300;
  margin: 0;
  text-align: left;
  line-height: 4.9rem; }

.main-subtitle {
  color: #808080;
  font-weight: 400;
  font-size: 1.8rem;
  margin: 0;
  text-align: left;
  line-height: 2.5rem; }

.title {
  color: #565a69;
  margin: 0; }
  .title_1 {
    font-size: 3.6rem;
    font-weight: 300;
    line-height: 4.9rem; }

.primary-input {
  width: 100%;
  max-height: 37px;
  border: 1px solid #cccccc;
  border-radius: 3px;
  padding: 6px 10px;
  font-size: 1.6rem;
  box-sizing: border-box; }
  .primary-input::-webkit-input-placeholder {
    color: #b3b3b3;
    font-size: 1.6rem; }
  .primary-input::-moz-placeholder {
    color: #b3b3b3;
    font-size: 1.6rem; }
  .primary-input:-ms-input-placeholder {
    color: #b3b3b3;
    font-size: 1.6rem; }
  .primary-input::-ms-input-placeholder {
    color: #b3b3b3;
    font-size: 1.6rem; }
  .primary-input::placeholder {
    color: #b3b3b3;
    font-size: 1.6rem; }
  .primary-input:focus {
    outline: none; }

.btn-ordinary {
  width: 100%;
  max-height: 37px;
  background-color: #27ccc0;
  outline: none;
  border: none;
  border-radius: 3px;
  color: #ffffff;
  box-sizing: border-box; }
  .btn-ordinary:hover {
    cursor: pointer;
    background-color: #1fa198; }
  .btn-ordinary__text {
    color: #ffffff;
    font-size: 1.6rem;
    font-weight: 700;
    text-align: center; }
  .btn-ordinary_remove {
    background: transparent;
    color: #ff305a;
    border: 1px solid #ff3159; }
    .btn-ordinary_remove:hover {
      border: 1px solid #97001d;
      color: #96001e;
      background: transparent; }

.primary-textarea {
  resize: none;
  max-width: 335px;
  height: 70px;
  border: 1px solid #cccccc;
  font-size: 1.6rem; }
  .primary-textarea:focus {
    outline: none; }

.main-form {
  display: flex; }
  .main-form_post {
    margin: 14px 0 7px;
    align-items: center;
    height: 37px; }
    .main-form_post .btn-ordinary {
      margin-left: 9px;
      max-width: 133px;
      height: 100%; }
    .main-form_post .primary-input {
      margin-left: 6px;
      height: 100%; }

.square {
  width: 52px;
  height: 52px;
  border: 1px solid #ffffff; }

.comment {
  padding: 15px 0;
  border-bottom: 1px solid #f2f2f2;
  display: flex;
  align-items: center;
  word-break: break-all; }
  .comment__description {
    color: #666666;
    font-weight: 400;
    font-size: 1.2rem;
    margin-left: 14px;
    text-align: left; }
  .comment__colored-square {
    flex: 0 0 52px; }
  .comment:nth-child(2n + 1) .comment__colored-square {
    background-color: #ff8a00; }
  .comment:nth-child(2n) .comment__colored-square {
    background-color: #47568c; }

.comments {
  max-width: 444px;
  width: 100%;
  padding: 8px 19px 14px 18px;
  border: 1px solid #f2f2f2;
  background-color: #ffffff;
  box-sizing: border-box; }
  .comments__content .comment:last-child {
    border-bottom: none; }
  .comments__title {
    margin-bottom: 20px;
    text-align: left; }

.main-sidebar {
  display: flex;
  flex-direction: column;
  flex: 0 0 271px;
  width: 271px;
  background: #2b2f3e;
  height: 100%;
  padding: 16px 27px;
  box-sizing: border-box; }

.main-content {
  display: flex;
  max-width: 1009px;
  padding: 34px 22px 34px 32px;
  margin: 0 auto; }

.post {
  padding: 13px 0 9px 6px;
  word-break: break-all;
  border-bottom: 1px solid #f2f2f2; }
  .post:hover {
    cursor: pointer; }
  .post_checked {
    position: relative; }
    .post_checked::before {
      background: #ff2f5a;
      content: '';
      height: 100%;
      left: -28px;
      position: absolute;
      width: 5px; }
  .post__inner {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .post__title-box {
    display: flex;
    align-items: center;
    margin: 6px 0; }
  .post__title {
    color: #2b2f3e;
    font-size: 1.6rem;
    font-weight: 700;
    line-height: 2.2rem;
    margin: 0; }
  .post__comment-count {
    padding: 2px 10px;
    background-color: #27ccc0;
    border-radius: 18px;
    margin-left: 8px;
    color: #ffffff;
    font-size: 1.3rem;
    font-weight: 700;
    display: flex;
    align-items: center;
    max-height: 22px; }
  .post__remove-btn {
    max-width: 97px;
    width: 100%;
    font-size: 1.6rem;
    font-weight: 300;
    min-height: 34px;
    margin-left: 16px; }

.posts {
  max-width: 479px;
  width: 100%;
  border: 1px solid #f2f2f2;
  background-color: #ffffff;
  padding: 8px 14px 10px 25px;
  margin-right: 20px;
  text-align: left;
  box-sizing: border-box; }

.textarea-box {
  display: flex; }
  .textarea-box__colored-square {
    background: #e6e6e6;
    flex: 0 0 52px; }
  .textarea-box__textarea {
    width: 100%;
    margin-left: 14px; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

